<template>
    <form @submit.prevent="submit" class="section">
        <div class="field">
            <label class="label">Name</label>
            <div class="control">
                <input class="input" required="required" type="text" v-model="setting.name" placeholder="Name">
            </div>
        </div> 
        <div class="field">
            <label class="label">Description</label>
            <div class="control">
                <input class="input" required="required" type="text" v-model="setting.desc" placeholder="Description">
            </div>
        </div> 
        <div class="field">
            <label class="label">Avatar</label>
            <div class="control">
                <input class="input" required="required" type="text" v-model="setting.avatar" placeholder="Avatar Url">
            </div>
        </div> 
        <div class="field is-grouped">
            <div class="control">
                <button type="submit" class="button is-link">保存设置</button>
            </div>
        </div>
    </form>
</template>
<script>
import { mavonEditor } from 'mavon-editor'
import {getSetting,updatesetting} from '../../api/index'
import 'mavon-editor/dist/css/index.css'
  export default {
    data() {
      return {
          setting:{}
      }
    },
    watch: {
    },
    methods: {
        fetchDetail(){
            getSetting((res)=>{
                if(res.length>0){
                    this.setting=res[0];
                }
            })
        },
        submit(){
            updatesetting(this.setting, (res)=>{
                if(res){
                    this.$router.push({path:'/admin/setting'});
                }
            })
        }
    },
    components: {
      mavonEditor
    },
    mounted(){
        this.fetchDetail();
    }
  };
</script>



