<template>
<aside class="menu">
    <template v-for="nav in navs">
    <p class="menu-label">
        {{nav.name}}
    </p>
    <ul class="menu-list">
        <li v-for="n in nav.children">
            <router-link :to="{path: `${n.url}`}" :class="{'is-active' : n.isactive}">{{n.title}}</router-link>
        </li>
    </ul>
    </template>
</aside>
</template>
<script>
export default {
  name: 'Menu',
  data () {
    return {
        navs:[
            {
                name: 'General',
                children:[
                    {
                        title: 'Dashboard',
                        url: '/admin/dashboard',
                        isactive:true
                    },
                    {
                        title: 'Blog',
                        url: '/admin/blog'
                    },
                    {
                        title: 'New Blog',
                        url: '/admin/newblog'
                    },
                    {
                        title: 'Events',
                        url: '/admin/event'
                    },
                    {
                        title: 'Pages',
                        url: '/admin/pages'
                    }
                ]
            },{
                name: 'Administration',
                children:[
                    {
                        title: 'Users',
                        url: '/admin/users'
                    },{
                        title: 'Setting',
                        url: '/admin/setting'
                    }
                ]
            }
        ]
    }
  },
  watch:{
    '$route' (to, from) {
        this.getActivedList();
    }
  },
  methods: {
      getActivedList(){
          var page=this.$route.path;
          var ismatched=false;
          for(let i=0,len=this.navs.length;i<len;i++){
              for(let j=0,k=this.navs[i]['children'].length;j<k;j++){
                  
                this.navs[i]['children'][j].isactive=false;
                if(page==this.navs[i]['children'][j].url){
                    this.navs[i]['children'][j].isactive=true;
                    ismatched=true;
                }
              }
          }
          if(ismatched){
              this.$forceUpdate();
          }
      }
  },
    mounted(){
        this.getActivedList();
    }
}
</script>



