<template>
<section class="content is-small">
    <h4>最近文章</h4>
    <ul class="has-text-left">
        <li v-for="item in articles" :key="item.id">
            <router-link class="item-link" :to="{path: `/blog/${item.objectId}`}">{{item.title}}</router-link>
        </li>
    </ul>
</section>
</template>
<style>
</style>

<script>
import {getBlogListByPage} from '../api/index'
export default {
  name: 'Archive',
  data(){
      return {
          page: 1,
          pageSize: 10,
          articles: []
      }
  },
  methods:{
    fetchList(){
        this.page=1;
        getBlogListByPage(this.page, this.pageSize, res=>{
            this.articles= res;
        })
    }
  },
    mounted(){
        this.fetchList();
    }
}
</script>


