<template>
    <section class="section">
        <div class="navbar-end">
            <router-link :to="{path: 'newblog'}" class="button is-primary">新建</router-link>
        </div>
        <table class="table is-striped is-fullwidth">
            <thead>
                <tr>
                    <th>
                    </th>
                    <th>id</th>
                    <th>title</th>
                    <th>time</th>
                    <th>action</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item,index) in articles" v-if="articles.length>0">
                    <td><input type="checkbox" /></td>
                    <td>{{index+1}}</td>
                    <td>
                        <router-link :to="{path: `/page/${item.objectId}`}">{{item.title}}</router-link>             
                    </td>
                    <td>{{item.createdAt}}</td>
                    <td>
                        <router-link :to="{path: `/admin/editblog/${item.objectId}`}">修改</router-link>  
                        <button @click="del(item.objectId)">删除</button>  
                    </td>
                </tr>
                <tr v-else>
                    <td colspan="5">加载中……</td>
                </tr>
            </tbody>
        </table>
    </section>
</template>
<script>
  import {getPageList, delblog} from '../../../api/index'
  export default {
    data() {
      return {
          articles: []
      }
    },
    watch: {
    },
    methods: {
        fetchList(){
            getPageList(res=>{
                this.articles= res;
            })
        },
        del(id){
            if(confirm('是否确认删除？')){
                delblog(id,res=>{
                    this.fetchList
                })
            }
        }
    },
    mounted(){
        this.fetchList();
    }
  };
</script>



