<template>
<section>
  <Nav></Nav>
  <div class="container section">
    <div class="columns">
      <div class="column is-3 section has-text-centered">
        <Avatar></Avatar>
        <hr>
        <Archive></Archive>
      </div>
      <div class="column is-9">
        <div class="content" v-if="article.title">
            <figure class="image" v-if="article.thumb">
              <!--<img :src="article.thumb">-->
              <img :data-src="article.thumb" @click="zoomImage" :src="article.thumb" />
            </figure>
            <div class="content-header has-text-centered">
              <h3 class="is-4 title">
                  {{article.title}}
              </h3>
              <p class="is-6 article-subtitle">{{article.author}} @ {{article.createdAt}}</p>
            </div>
            <p class="section"><vue-markdown :source="article.content"></vue-markdown></p>
        </div>
        <div class="container section loading" v-else><span class="_loader"></span></div>           
      </div>
    </div>
  </div>  
  <Footer></Footer>
</section>
 
</template>
<script>
  import Nav from '../../components/nav'
  import Archive from '../../components/archive'
  import Footer from '../../components/footer'
  import {getBlogDetail } from '../../api/index'
  import VueMarkdown from 'vue-markdown'
  import Avatar from '../../components/avatar'

  export default {
    data() {
      return {
          id: '',
          article: {}
      }
    },
    watch: {
    },
    methods: {
        fetchDetail: function(){
            var that=this;
            this.id = this.$route.params.id;
            getBlogDetail(this.id, res=>{
                if(res.length>0){
                    that.article=res[0];
                }
            })
        },        
        zoomImage(e){
          let src=e.currentTarget.dataset.src;
          this.$zoom(src);
        }
    },
    components: {
      Nav,
      Footer,
      VueMarkdown,
      Archive,
      Avatar
    },
    mounted(){
        this.fetchDetail();
    }
  };
</script>



