<template>
    <section class="section">
        <div class="navbar-end">
            <router-link :to="{path: 'newblog'}" class="button is-primary">新建</router-link>
        </div>
        <table class="table is-striped is-fullwidth">
            <thead>
                <tr>
                    <th>
                        <input type="checkbox" id="checkall" />
                        <label for="checkall">全选</label>
                    </th>
                    <th>id</th>
                    <th>用户名</th>
                    <th>手机号</th>
                    <th>Email</th>
                    <th>action</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item,index) in users">
                    <td><input type="checkbox" /></td>
                    <td>{{index+1}}</td>
                    <td>
                        {{item.username}}             
                    </td>
                    <td>{{item.mobilePhoneNumber}}</td>
                    <td>{{item.email}}</td>
                    <td>
                        <router-link :to="{path: `/blog/edit?id=${item.objectId}`}">删除</router-link>  
                        <router-link :to="{path: `/admin/editblog/${item.objectId}`}">修改</router-link>  
                    </td>
                </tr>
            </tbody>
        </table>
    </section>
</template>
<script>
  import {getUserList} from '../../api/index'
  export default {
    data() {
      return {
          totalSize: 0,
          users: []
      }
    },
    watch: {
    },
    methods: {
        fetchList(){
            //this.page=this.$router.query.page || 0;
            getUserList(res=>{
                this.users= res;
                this.totalSize= res.length;
            })
        }
    },
    mounted(){
        this.fetchList();
    }
  };
</script>



