<style scoped>
.sponsors {
  padding: 0.75rem;
}
.category .iconfont {
  font-size: 80px;
  color: #888;
}
.category .title {
  text-transform: uppercase;
}
.sponsors .article {
  display: block;
  padding: 15px;
}
.sponsors .column {
  align-items: center;
  display: flex;
  justify-content: center;
  position: relative;
  overflow: hidden;
}
.sponsors .column .text{
  position: absolute;
  height:100%;
  width: 100%;
  background-color: rgba(0,0,0,.5);
  text-align: center;
  color: #fff;
  transition: top .2s;
  overflow: hidden;
  text-overflow: ellipsis;
  top: 160px;
}
.sponsors .column:hover .text{
  top: 0;
  padding: 0 10px;
}
.sponsors .column .text h6{
  font-size: 26px;
  padding: 20px 0;
  text-decoration: underline;
  font-weight: 800;
}
.sponsors img {
