}
.em-svg{
  width: 10em;
  height: 10em;
}
section.marathon>.container>.columns,section.reading>.container>.columns{
  flex-direction: row-reverse;
}
@media (max-width: 414px) {
  .index-content {
    text-align: center;
  }
  img.hero-img {
