<template>
    <section class="section">
        <div class="navbar-end">
            <router-link :to="{path: 'newblog'}" class="button is-primary">新建</router-link>
        </div>
        <table class="table is-striped is-fullwidth">
            <thead>
                <tr>
                    <th>
                        <input type="checkbox" id="checkall" v-bind:checked="checkall" />
                        <label for="checkall">全选</label>
                    </th>
                    <th>id</th>
                    <th>title</th>
                    <th>time</th>
                    <th>action</th>
                </tr>
            </thead>
            <tbody v-if="articles.length>0">
                <tr v-for="(item,index) in articles" :key="item.index">
                    <td><input type="checkbox" v-bind:checked="checkall" /></td>
                    <td>{{index+1}}</td>
                    <td>
                        <router-link :to="{path: `/blog/${item.objectId}`}">{{item.title}}</router-link>             
                    </td>
                    <td>{{item.createdAt}}</td>
                    <td>
                        <router-link :to="{path: `/admin/editblog/${item.objectId}`}">修改</router-link>  
                        <button @click="del(item.objectId)">删除</button>  
                    </td>
                </tr>
            </tbody>
            <tbody v-else>
                <tr>
                <td colspan="5">加载中……</td>
                </tr>
            </tbody>
        </table>
        <nav class="pagination is-small" role="navigation" aria-label="pagination">
            <a class="pagination-previous" title="This is the first page" v-bind:disabled="page==1">Previous</a>
            <a class="pagination-next" v-bind:disabled="page==pageCount">Next page</a>
            <ul class="pagination-list">
                <li v-for="num in pageCount" :key="num.index">
                    <router-link class="pagination-link" :class="{'is-current' : page==num}" :to="{path: `/admin/blog/?page=${num}`}" @click="changePage(num)">{{num}}</router-link>                    
                </li>
                <li>共 {{totalSize}} 条数据</li>
            </ul>
        </nav>        
    </section>
</template>
<script>
  import {getBlogListByPage, delblog} from '../../../api/index'
  export default {
    data() {
      return {
          checkall: false,
          page:1,
          pageSize:20,
          pageCount:1,
          totalSize: 0,
          articles: []
      }
    },
    watch: {
    },
    methods: {
        changePage(page){
            this.page=page;
            this.$emit("reload");
        },
        fetchList(){
            this.page=this.$route.query.page || 1;
            getBlogListByPage(this.page, this.pageSize, res=>{
                this.articles= res;
                this.totalSize= res.length;
                this.pageCount= Math.floor(this.totalSize/this.pageSize)+1;
            })
        },
        del(id){
            if(confirm('是否确认删除？')){
                delblog(id,res=>{
                    this.fetchList
                })
            }
        }
    },
    mounted(){
        this.fetchList();
    }
  };
</script>



