<template>
    <form @submit.prevent="submit" class="section">
        <div class="field">
            <label class="label">Title</label>
            <div class="control">
                <input class="input" required="required" type="text" v-model="blog.title" placeholder="Text input">
            </div>
        </div> 
        <div class="field">
            <label class="label">Thumbnail</label>
            <div class="control">
            <img v-bind:src="blog.thumb" />
            </div>
        </div>
        <div class="field">
            <label class="label">Content</label>
            <div class="control">
                <mavon-editor v-model="blog.content"/>
            </div>
        </div>
        <div class="field is-grouped">
            <div class="control">
                <button type="submit" class="button is-link">保存并发布</button>
            </div>
        </div>
    </form>
</template>
<script>
import { mavonEditor } from 'mavon-editor'
import {getBlogDetail, updateblog} from '../../../api/index'
import 'mavon-editor/dist/css/index.css'
  export default {
    data() {
      return {
          blog:{},
          author: 'migao',
          id: ''
      }
    },
    watch: {
    },
    methods: {
        fetchBlog(){
            this.id = this.$route.params.id;
            getBlogDetail(this.id,(res)=>{
                if(res.length>0){
                    this.blog=res[0];
                }
            })
        },
        submit(){
            updateblog(this.blog, (res)=>{
                if(res){
                    this.$router.push({path:'/admin/blog'});
                }
            })
        }
    },
    components: {
      mavonEditor
    },
    mounted(){
        this.fetchBlog();
    }
  };
</script>



