import Router from 'vue-router'
import Index from '@/views/index'
import Login from '@/views/login'
import Blog from '@/views/blog/blog'
import Gallery from '@/views/gallery'
import About from '@/views/about'
import Event from '@/views/event/events'
import BlogDetail from '@/views/blog/detail'
import AdminBasePage from '@/views/admin/base'
import Dashboard from '@/views/admin/dashboard'
import AdminBlog from '@/views/admin/blogs/blog'
import AdminEvent from '@/views/admin/events/event'
import NewAdminEvent from '@/views/admin/newevent'
import AdminUser from '@/views/admin/user'
import NewAdminBlog from '@/views/admin/blogs/newblog'
import EditAdminBlog from '@/views/admin/blogs/editblog'
import adminSetting from '@/views/admin/setting'
import Pages from '@/views/admin/pages/pages'
import NotFoundComponent from '@/views/404'

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '*', 
      component: NotFoundComponent 
    },
    {
      path: '/',
      name: 'Index',
      component: Index,
      meta:{
        title: '首页'
      }
    },
    {
      path: '/blog',
      name: 'Blog',
      component: Blog,
      meta:{
        title: '博客'
      }
    },
    {
      path: '/event',
      name: 'Event',
      component: Event,
      meta:{
        title: '比赛'
      }
    },
    {
      path: '/gallery',
      name: 'Gallery',
      component: Gallery,
      meta:{
        title: '照片'
      }
    },
    {
      path: '/about',
      name: 'About',
      component: About,
      meta:{
        title: '关于'
      }
    },
    {
      path: '/blog/?page=:id',
      name: 'BlogPage',
      component: Blog,
      meta:{
        title: '博客'
      }
    },
    {
      path: '/blog/:id',
      name: 'BlogDetail',
      component: BlogDetail
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta:{
        title: '登录'
      }
    },
    /*{
      path: '/admin',
      component: AdminBasePage,
      children:[{
        path:'dashboard',
        name:'dashboard',
        component: Dashboard,
        meta:{
          title: '控制台'
        }
      },{
        path:'setting',
        name:'setting',
        component: adminSetting
      },{
        path:'blog',
        name:'blog',
        component: AdminBlog
      },{
        path:'blog/?p=:id',
        name:'blogpaged',
        component: AdminBlog
      },{
        path:'newblog',
        name:'newblog',
        component: NewAdminBlog
      },
      {
        path: 'editblog/:id',
        name: 'EditAdminBlog',
        component: EditAdminBlog
      },{
        path:'event',
        name:'adminevent',
        component: AdminEvent
      },{
        path:'event/?p=:id',
        name:'eventpaged',
        component: AdminEvent
      },{
        path:'newevent',
        name:'newevent',
        component: NewAdminEvent
      },{
        path:'users',
        name:'user',
        component: AdminUser
      },{
        path:'pages',
        name:'pages',
        component: Pages
      }]
    }*/
  ]
})



