<template>
<section class="main">
  <Nav></Nav>
  <div class="container section">
    <div class="columns" v-if="setting.about">
      <div class="column is-3">
        <section class="has-text-centered">
          <Avatar></Avatar>
          <hr />
          <About v-on:loadAbout="changeTab"></About>
        </section>
      </div>
      <div class="column is-9">
        <div class="content">
          <div id="about" name="about" class="tab_content" v-bind:class='currentTab=="about"? "current" : ""'>
            <vue-markdown :source="setting.about"></vue-markdown>
          </div>
          <div id="sport" name="sport" class="tab_content timeline" v-bind:class='currentTab=="sport"? "current" : ""'>
            <vue-markdown :source="setting.sport"></vue-markdown>
          </div>
          <div id="contact" name="contact" class="tab_content" v-bind:class='currentTab=="contact"? "current" : ""'>
            <vue-markdown :source="setting.contact"></vue-markdown>
          </div>
        </div>
      </div>
    </div>
    <div class="container section loading" v-else><span class="_loader"></span></div>   
  </div>
  <Footer></Footer>
</section>
</template>

<style scoped>
.main{
  min-height: 100vh;
}
.container.section{
  flex: 1;
}
.tab_content{
  padding:10px 0;
  display: none;
  transition: all .2s;
}
.tab_content.current{
  display: block;
}
.tab_content.timeline{
  border-left:none;
}
.tab_content h4{
  font-size:14px;
}
.tab_content ul{
  list-style-type: none;
  margin-left:1em;
}
.tab_content li{
  position: relative;
  padding-left: 20px;
}
.tab_content li:before{
    content: '';
    position: absolute;
    border-color: #3c81df;
    border-width: 4px;
    border-style: solid;
    border-radius: 50%;
    width: 15px;
    height: 15px;
    margin-left: -8px;
    top: 5px;
    left: 0;
}
</style>
<script>
  import Avatar from '../components/avatar'
  import Nav from '../components/nav'
  import Footer from '../components/footer'
  import About from '../components/about'
  import {getSetting} from '../api/index'; 
  import VueMarkdown from 'vue-markdown'
  export default {
    data() {
      return {
        setting:{},
        currentTab: 'about'
      }
    },
    props:['tab'],
    watch: {
    },
    methods: {
      fetchList(){
        getSetting((res)=>{
          if(res.length>0){
            this.setting= res[0];
          }
        })
      },
      changeTab(tab){
        this.currentTab=tab;
      }
    },
    components: {
      Nav,
      About,
      VueMarkdown,
      Footer,
      Avatar
    },
    mounted(){
      this.fetchList();
    }
  };
</script>


